.funds-dispursed-title {
  white-space: nowrap;
  font-size: 1.5em;
  font-weight: 600;
  margin-top: 0px;
}

.analysis-country-content {

  #GHSAButton {
    position: absolute;
    top: 104px;
    right: 30px;
  }

  .selectCurrencyContainer {
    position: absolute;
    top: 100px;
    right: 85px;
    display: inline-block;
  }

  .selectCurrencyContainer a:hover {background-color: #ddd;}

  .show {display: block;}

  .selectCurrency {
    margin-bottom: 5px;
  }

  .curDropdownContent {
    display: none;
    //position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }

  .curDropdownContent a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  #currencyChoice {
    position: absolute;
    vertical-align: middle;
    display: inline-block;
    font-size: 10px;
    border-radius: 4px;
    width: 180px;
    height: 40px;
    background: #f9f9f9;
    border: solid 1px #b3b3b3;
    color: black;
    //text-align: left;
    //left:10px;
  }

  #currencyChoice p {
    position: absolute;
    top:7px;
    left: 10px;
  }

  .currencyOption {
    background-color: #3498DB;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
  }

  .currencyOption:hover, .currencyOption:focus {
    background-color: #2980B9;
  }

  .ghsa-toggle-options-container {
    position: absolute;
    top: 95px;
    right: 160px;
  }
  .ghsa-toggle-options {
    top: 0;
    margin: 10px 0 0 5px;
    &.below-switch-button {
      position: relative;
      left: 910px;
      top: 43px;
    }
  }
  .analysis-country-title {
    display: grid;
    // display: inline-block;
    position: relative;
    text-align: left;
    background-color: #f2f2f2;
    height: 100px;
    width: 100%;
    line-height: 1.25;
    cursor: pointer;
    &.country-page {
      cursor: default;
    }

    .analysis-country-flagAndName {
      display: grid;
      grid-gap: 0 20px;
      grid-auto-flow: column;
      justify-content: start;
      align-items: center;
      align-self: center;
      font-size: 30px;
      font-weight: 900;
      font-style: "raleway";
      margin-left: 30px;
    }

    img.ghsa-info-img {
      width: 18px;
      height: 18px;
    }
  }

  .profile {
    //position:relative;
  }

  .profile-type-container {
    height: 90px;
    //background-color: #762a83;
    font-size: 14px;
    font-weight: 400;
    font-color: #ffffff;
    text-align: left;
    line-height: 2;
    vertical-align: middle;
  }

  .profile-type-text {
    display: inline-block;
    color: white;
    margin-left: 30px;
    margin-top: 27px;
    font-family: "raleway";
    font-weight:150px;
    font-size: 18px;
  }


  .flag {
    // height: 35px;
    // /* width: 96px; */
    // /* transform: scale(0.47); */
    // vertical-align: middle;
    // /* transform-origin: center left; */
    // /* object-fit: cover; */

    // height: 64px;
    // width: 96px;
    height: 25px;
    width: 37.5px;
    // transform: scale(0.47);
    vertical-align: middle;
    // transform-origin: center left;
    object-fit: cover;

  }


  .flag.globe {
    border: none;
  }
  div {
    .btn-sm {
      font-size: 0.8em;
      width: 125px;
    }
  }
}


