/* ------------------- */
/* Generic Map Styling */
/* ------------------- */
.overlay {
	fill: none;
	pointer-events: all;

    rect {
       box-sizing: content-box;
    }
}
.country {
  fill: #ccc;
  // stroke: none;
  stroke: $lightCountryBorder;
	&.dark { stroke: $darkCountryBorder; }
  stroke-width: 0.5;
  &:hover {
  	stroke: $hoverCountryBorder;
  	// stroke: rgba(255,255,255,0.8);
  }
  &.active {
    stroke: $activeCountryBorder;
    // stroke: rgba(255,109,0,0.8);
  }
}
.boundary {
  fill: none;
  stroke: none;
}
.country-link {
  fill: none;
  stroke: steelblue;
  stroke-linecap: round;
  opacity: 0.2;
}


/* --- Map Legends --- */
.legend-bar {
}
.legend-text {
  text-anchor: middle;
}
.dark .legend-text, .dark .legend-start-label, .dark .legend-title, .dark .collapse-arrow {
  fill: #eeeeee;
}
.legend-title {
  font-size: 1.1em;
  text-anchor: middle;
}

/* --- Map Viewport--- */
.viewport-base,
.viewport-cutout {
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 0;
}

.viewport-base {
  stroke: #000;
  fill: #000;
}

.viewport-cutout {
  stroke: #c4c6c6;
}

.viewport-edge {
  stroke: #cccccc;
  fill: none;
  stroke-width: .25px;
}

/* --- Map Non-country Funder/Recipient Lists --- */
.non-country-list-container {
  color: #f7f8f8;
  // color: #c4c6c6;
  z-index: -99;
  width: 320px;
  position: fixed;
  top: 0; // this is dynamically set
  &.right {
    right: 20px;
    transform-origin: right top;
    -moz-transform-origin: right top;
  }
  &.left {
    left: 20px;
    transform-origin: left top;
    -moz-transform-origin: left top;
  }
  .nicescroll-rails {
    background: none;
    border: 2px solid #474747;
    border-radius: 3px;
    display: block !important;
    top: 60px !important;
  }
  .non-country-list {
    height: 680px;
    // height: 560px;
    // max-height: 560px;
    overflow-y: scroll;
    overflow-x: hidden;
    line-height: 2.5;
    transform: scale(1);

    &::-webkit-scrollbar {
     width: 0.75em;
     height: 0.75em;
   }
   &::-webkit-scrollbar-track, .nicescroll-rails {
    background: none;
    border: 2px solid #474747;
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
   background-color: #5a5b5b;
   border-radius: 3px;

   &:hover {
     background-color: #5a5b5b;
   }
 }

 &.recipient-list {
  text-align:left;
}

&.funder-list {
  text-align: right;
  direction: rtl;
}

